const Base = require('../base.js');
//添加管理员
module.exports = class extends Base {
    async indexAction() {
        const model = this.model('user_info');
        const result = await model.thenAdd(this.ctx.post(), {
            user_name: this.ctx.post('user_name')
        });
        var jhde = {}
        if (result.type == "exist") { //已存在
            jhde.code = -1
            jhde.msg = "账号已存在，请勿重复添加"
        } else {
            jhde.code = 0
            jhde.msg = "账号添加成功"
        }
        return this.fail(jhde); //2输出json 推荐

    }

};
